<template>
	<view :class="['activity-detail',activityStatus]">
		<image :src="item.image" class="pic" />
		<view class="content">
			<view class="item">
				活动主题：
				<text class="theme">{{item.title}}</text>
				<text class="status">
					{{item.status==='in_start'?'进行中':(item.status==='end'?'已结束':'未开始')}}
				</text>
			</view>
			<view class="item">活动时间：
				<text class="date" :class="{end:end}">
					{{item.start_time}}-{{item.end_time}}
				</text>
			</view>
			<view class="item">活动形式：<text class="type" :class="{end:end}">{{item.type}}</text></view>
			<!-- <view class="item">参与名额：<text class="limit"  :class="{end:end}">{{item.limit}}</text>
				 <text class="isReported" :class="{end:end}">已有{{item.isReported}}人报名参加</text> 
			</view> -->
			<view class="item">活动介绍：
				<view class="desc">{{item.description}}</view>
			</view>
			<button type="primary" 
				@click="joinActivity" 
				class="btn" 
				v-if="joined"
				:class="{over:end}"
				:disabled="item.is_sign==1">
				我要参与
			</button>
		</view>
		<Kefu />
		<Modal ref="modal" :status="modalStatus" title="参与成功" desc="" />
	</view>
</template>

<script>
	import Kefu from '@/components/Kefu'
	import Modal from '@/components/Modal'
	export default {
		components: {
			Kefu,
			Modal
		},
		data() {
			return {
				token:'',
				id:-1,
				joined:true,
				end:true,
				item: {
					/* pic: '../../static/image/activity-pic.png',
					status: '已开始',
					theme: 'AMD 618促销狂欢季全新上线',
					date: '06.01 12:00~06.18 24:00',
					type: '线上活动',
					limit: '50人',
					isReported: '40',
					ing:'true',
					desc: '以下是活动介绍以下是活动介绍以下是活动介绍以下是活动 介绍以下是活动介绍以下是活动介绍以下是活动介绍以下是 介绍以下是活动介绍', */
				},
				modalStatus: 'success',
				activityStatus: 'online', //活动状态 online 在线 not-yet还没开始 over过期了
			};
		},
		//路由参数就收
		onLoad(opt) {
			let token = uni.getStorageSync('token')
			this.token=token;
			console.log(opt)
			this.id=opt.id;
			if(opt.joined){
				this.joined = false;
			}
			this.getDetail(opt.id,token)
		},
		methods: {
			showJoin(status){
				console.log(status)
			},
			joinActivity() {
				// this.$refs.modal.open()
				let _this=this;
				uni.request({
					url:'https://amd.mcooks.cn/api/event/sign',
					method:'POST',
					header:{
						'authtoken':'token '+_this.token,
					},
					data:{
						id:_this.id
					},
					success(res){
						console.log(res.data)
					}
				})
			},
			getDetail(id,token){
				let _this=this;
				uni.request({
					url:'https://amd.mcooks.cn/api/events/show',
					method:'POST',
					header:{
						'authtoken':'token '+token,
					},
					data:{
						id
					},
					success(res){
						console.log(res.data)
						if(res.data.data.is_sign==0){
							_this.end= false;
						}
						_this.item=res.data.data;
					}
				})
			}
		},
	}
</script>

<style lang="scss" scoped>
	.over{
		background-color: #5E5C5C;
	}
	.activity-detail {
		height: calc(100vh - 88rpx);
		display: flex;
		flex-direction: column;
		justify-content: flex-start;

		.pic {
			height: 350rpx;
			width: 100%;
		}

		.content {
			position: relative;
			padding: 30rpx;
			background: #fff;
			flex: 1;

			.item {
				font-size: 26rpx;
				font-weight: 500;
				color: rgba(102, 102, 102, 1);
				margin-bottom: 30rpx;
				line-height: 1;

				.theme {
					font-size: 26rpx;
					font-weight: bold;
					color: rgba(51, 51, 51, 1);
				}

				.status {
					font-size: 22rpx;
					font-weight: 500;
					color: rgba(243, 157, 35, 1);
					float: right;
				}

				.date,
				.type {
					font-size: 26rpx;
					font-weight: 500;
					color: rgba(243, 101, 35, 1);
				}

				.limit {
					font-size: 26rpx;
					font-weight: 500;
					color: rgba(243, 157, 35, 1);
				}

				.isReported {
					font-size: 22rpx;
					font-weight: 500;
					color: rgba(255, 76, 76, 1);
					float: right;
				}

				.desc {
					margin-top: 30rpx;
					font-size: 26rpx;
					font-weight: 500;
					color: rgba(51, 51, 51, 1);
					line-height: 48rpx;
				}
				.end{
					color:#333;
				}
			}

			.btn {
				height: 64rpx;
				line-height: 64rpx;
				background-color: rgba(243, 101, 35, 1);
				border-radius: 10rpx;
				position: absolute;
				bottom: 20rpx;
				width: calc(100% - 60rpx);
			}
		}
	}

	.not-yet,
	.over {
		filter: grayscale(100%);
	}
</style>